.card {
	max-width: 256px;
	max-height: auto;
	margin: 15px 45px;
	-webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	        box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	border: 3px solid white;
	border-radius: 15px;
}

.card:hover {
	-webkit-transform: scale(1.10);
	    -ms-transform: scale(1.10);
	        transform: scale(1.10);
}

img {
	border-radius: 15px;
}



